import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import Loader from 'Shared/components/Loader/Loader'
import { CandidateProfileViewLoader, CandidatesDashboardLoader } from 'Shared/components/Main/MainLoaders'
import withTranslations from 'Shared/hoc/withTranslations'

const CandidateAppLoader = (props) => {
  const renderLoader = () => <Loader className='loader--app' description={`${props.t('common.loading')}...`} />

  return (
    <Switch>
      <Route exact path='/candidate/oliv-plus' component={renderLoader} />
      <Route path='/candidate/chat' component={renderLoader} />
      <Route path='/candidate/profile/complete' component={renderLoader} />
      <Route exact path='/candidate/dashboard' component={CandidatesDashboardLoader} />
      <Route exact path='/candidate/profile/preview' component={CandidateProfileViewLoader} />
      <Route exact path='/candidate/oliv-plus/review' component={renderLoader} />
      <Route exact path='/candidate/oliv-plus/checkout' component={renderLoader} />
      <Route path='/candidate/settings' component={renderLoader} />
      <Redirect to='/not-found' />
    </Switch>
  )
}

export default withTranslations(CandidateAppLoader)



