import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { logout, checkImpersonation } from 'Shared/ducks/user'
import { fetchUserIfNeeded } from 'Shared/ducks/user/candidate'
import { mergeProps } from 'Shared/helpers/redux'
import App from 'Candidate/components/App'

const mapStateToProps = state => ({
  user: state.user
})

const mapDispatchToProps = dispatch => ({
  logout: () => dispatch(logout()),
  checkImpersonation: () => dispatch(checkImpersonation()),
  getInitialData: () => dispatch(fetchUserIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps)(App))



