import React, { Component } from 'react'
import { Route, Switch, Redirect } from 'react-router-dom'
import PropTypes from 'prop-types'
import appRegistry from 'Shared/services/AppRegistry'
import withTranslations from 'Shared/hoc/withTranslations'
import loadable from 'Shared/helpers/loadable'
import { CandidateProfileViewLoader, CandidatesDashboardLoader } from 'Shared/components/Main/MainLoaders'
import CandidateAppLoader from './CandidateAppLoader'

const DashboardViewConnector = loadable(() => import(/*  webpackChunkName: "DashboardViewConnector" */ 'Candidate/connectors/Views/DashboardViewConnector'), CandidatesDashboardLoader)
const CandidateProfileViewConnector = loadable(() => import(/*  webpackChunkName: "CandidateProfileViewConnector" */ 'Candidate/connectors/Views/CandidateProfileViewConnector'), CandidateProfileViewLoader)
const ChatAppConnector = loadable(() => import(/*  webpackChunkName: "ChatAppConnector" */ 'Chat/connectors/Components/ChatAppConnector/ChatAppConnector'))
const CandidateSettingsViewConnector = loadable(() => import(/*  webpackChunkName: "CandidateSettingsView" */ 'Candidate/connectors/SettingsView/SettingsViewConnector'))
const CompleteProfileView = loadable(() => import(/* webpackChunkName: "CompleteProfileView" */ 'Candidate/views/CompleteProfileView'))
const OlivPlusLandingPageConnector = loadable(() => import(/* webpackChunkName: "OlivPlusLandingPage" */ 'Candidate/connectors/OlivPlus/OlivPlusLandingPageConnector'))
const OlivPlusMaintenance = loadable(() => import(/* webpackChunkName: "OlivPlusMaintenance" */ 'Shared/layouts/OlivPlusMaintenance/OlivPlusMaintenance'))

class App extends Component {
  static propTypes = {
    logout: PropTypes.func.isRequired,
    user: PropTypes.object.isRequired
  }

  constructor () {
    super(...arguments)

    this.state = {
      ready: false
    }
  }

  async componentDidMount () {
    try {
      this.setState({ ready: true })
    } catch (error) {
      appRegistry.logger.error(error)

      // this.props.logout()
    }
  }

  render () {
    return this.state.ready
      ? (
        <Switch>
          <Route exact path='/candidate/oliv-plus' component={OlivPlusLandingPageConnector} />
          <Route path='/candidate/chat' component={ChatAppConnector} />
          <Route path='/candidate/profile/complete' component={CompleteProfileView} />
          <Route exact path='/candidate/dashboard' component={DashboardViewConnector} />
          <Route exact path='/candidate/profile/preview' component={CandidateProfileViewConnector} />
          <Route exact path='/candidate/oliv-plus/review' component={OlivPlusMaintenance} />
          <Route exact path='/candidate/oliv-plus/checkout' component={OlivPlusMaintenance} />
          <Route path='/candidate/settings' component={CandidateSettingsViewConnector} />
          <Redirect to='/not-found' />
        </Switch>
      )
      : <CandidateAppLoader />
  }
}

export default withTranslations(App)



